@import '../utils/utils.scss';

.faq {
  background-color: $faq-background;
  height: 1000px;

  &__title {
    @include title;
  }

  &__description {
    @include description;
    max-width: 490px;
  }

  &__info-block {
    height: 100px;
    width: 100%;
    border-radius: 10px;
    background-color: aquamarine;

    &__header {
      display: flex;
      align-items: center;

      &__title {
        @include subtitle;
      }
      &__description {
        @include description;
      }
    }

    &__content {
      padding: 60px;
      background-color: rgb(117, 66, 66);
    }
  }
}